.about-section {
  background-image: linear-gradient(to right bottom, rgba(139, 156, 253, 0.363), rgba(190, 6, 6, 0.411));
}
.about__card {
  transition: all;
}
.about__card:hover {
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.438);
}

.features__img {
  height: 250px;
  transition: all 0.5s;
}
.features__img:hover {
  outline: 1.5rem solid #55c57a;
  -webkit-transform: scale(1.05) translateY(-0.5rem);
          transform: scale(1.05) translateY(-0.5rem);
}


.header {
  height: 95vh;
  background-image: linear-gradient(to right bottom, rgba(213, 111, 111, 0.438), rgba(255, 249, 165, 0.596)), url(/static/media/header-bg.ce7c8183.jpg);
  background-size: cover;
  background-position: bottom;
  position: relative;
}
.header__text-box {
  position: absolute;
  top: 40%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
}
.heading-primary {
  color: #fff;
  text-transform: uppercase;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  margin-bottom: 6rem;
}
.heading-primary--main {
  display: block;
  font-size: 60px;
  font-weight: 400;
  letter-spacing: 20px;
}
.heading-primary--sub {
  display: block;
  font-size: 20px;
  font-weight: 700;
  letter-spacing: 10px;
}

.review-box::before {
  content: "\201C";
  font-size: 390%;
  display: block;
  position: absolute;

  top: -15px;
  left: 5px;
}

#side-bar {
  background-color: #203d37;
  min-height: 100vh;
  color: white;
}
.side-bar__brand {
  padding: 20px 0 0 30px;
}
.side-bar-list {
  list-style: none;
  margin: 30px 0 0 0;
  padding: 0;
}
#side-bar .side-bar-list a {
  text-decoration: none;
}
.side-bar-list-item {
  padding: 10px 30px;
  color: white;
  font-size: 20px;
}
.side-bar-list-item:hover,
.side-bar-list-item:active {
  background-color: #366e63;
}

